export default {
	name: 'login',
	data() {
		return {
			tabUrls: [
				'/pages/mine/index',
				'/pages/order/OrderAllList'
			]
		}
	},
	methods: {
		login () {
			uni.login({
				provider: 'weixin',
				success: (loginData) => {
					uni.getUserInfo({
						provider: "weixin",
						fail(error) {
							this.$toast('微信登录失败')
						},
						success: (res) => {
							this.$http(this.$urlPath.wxLogin, {
								code: loginData.code,
								userInfo: JSON.stringify(res.userInfo)
							}, '正在登录…', (data) => {
								let info = data.data.userinfo
								this.$userInfo.saveUserInfo(info)
								let backPage = getApp().globalData.backPage
								if (backPage) {
									if (this.tabUrls.includes(backPage)) {
										uni.switchTab({
											complete: () => {
												getApp().globalData.backPage = null
												uni.$emit('userInfo')
											},
											url: backPage
										})
									} else {
										uni.redirectTo({
											complete: () => {
												getApp().globalData.backPage = null
												uni.$emit('userInfo')
											},
											url: backPage
										})
									}
								} else {
									this.$back()
								}
							}, (code, msg) => {
								this.$toast(msg)
							})
						}
					})
				}
			})
		}
	}
}
